.fieldset--achievements {
  width: 100%;
  padding: 42px 0 38px;

  @media (min-width: $tablet) {
    width: 620px;
    margin: 0 auto 59px;
    padding: 48px 0 44px;
    border: 2px solid #E5E5E5;
    order: 4;
  }
}

.fieldset--achievements.fieldset--grey {
  @media (min-width: $tablet) {
    background-color: transparent;
  }
}

.fieldset--achievements .fieldset__label {
  display: flex;
}

.fieldset--achievements .fieldset__list {
  width: 280px;
  margin: 75px auto 0;

  @media (min-width: $tablet) {
    width: auto;
    margin: 0;
    margin-left: 58px;
  }
}

.fieldset--achievements .fieldset__item {
  &:not(:last-child) {
    margin-bottom: 43px;
  }
}

.fieldset--achievements .fieldset__input {
  position: absolute;
  width: 0;
  height: 0;
  min-height: 0;
  margin: 0;
  appearance: none;
  border: none;
}

.fieldset--achievements .fieldset__text-value {
  margin: 0;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
}

.fieldset--achievements .fieldset__checkbox {
  position: relative;
  top: -4px;
  height: 39px;
  width: 39px;
  margin-right: 20px;
  border: 2px solid #E5E5E5;
  background: #FFFFFF;
  display: block;
}

.fieldset--achievements .fieldset__input:checked ~ .fieldset__checkbox {
  background: #FFFFFF url('../../../../img/decorative/tick.svg') center no-repeat;
}
